.content-wrapper,
.content-layer-1,
.content-layer-2 {
  position: relative;
  z-index: 1;
  background: #2F2F2F;
  box-shadow: 0 -10px 30px 0 rgba(0, 0, 0, 0.15);
}

.content-layer-1 {
  z-index: 2;
  margin-top: 235px;
}

.content-layer-2 {
  z-index: 3;
}

.hidden-text {
  font: 0/0 a;
  text-shadow: none;
  color: transparent;
}

.lnk {
  color: inherit;
  transition: color 0.2s ease;
}

.lnk:hover, .lnk:focus {
  color: #21DBB2;
}

.text-gradient {
  color: #21DBB2;
}

.m-backgroundcliptext .text-gradient {
  background-image: linear-gradient(20deg, #20D8A2 0%, #21E0C7 100%);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  text-fill-color: transparent;
}

.fade-in {
  transform: translate3d(0, 100px, 0);
  opacity: 0;
  transition: 0.5s ease-out;
  transition-property: transform, opacity;
}

.fade-in.animate {
  transform: none;
  opacity: initial;
}

.borders {
  position: absolute;
  z-index: 0;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  pointer-events: none;
}

.borders.-width-3 .border-top,
.borders.-width-3 .border-bottom {
  height: 3px;
}

.borders.-width-3 .border-right,
.borders.-width-3 .border-left {
  width: 3px;
}

.borders.-width-6 .border-top,
.borders.-width-6 .border-bottom {
  height: 6px;
}

.borders.-width-6 .border-right,
.borders.-width-6 .border-left {
  width: 6px;
}

.borders.-width-8 .border-top,
.borders.-width-8 .border-bottom {
  height: 8px;
}

.borders.-width-8 .border-right,
.borders.-width-8 .border-left {
  width: 8px;
}

.borders.-anim-one-way .border-top:before,
.borders.-anim-one-way .border-right:before,
.borders.-anim-one-way .border-bottom:before,
.borders.-anim-one-way .border-left:before {
  transition-duration: 0s;
}

.draw .borders.-anim-one-way .border-top:before, .draw
.borders.-anim-one-way .border-right:before, .draw
.borders.-anim-one-way .border-bottom:before, .draw
.borders.-anim-one-way .border-left:before {
  transition-duration: 0.3s;
}

.borders.-anim-one-way .border-top:before,
.borders.-anim-one-way .border-left:before {
  transition-delay: 0s;
}

.draw .border-top:before,
.draw .border-right:before,
.draw .border-bottom:before,
.draw .border-left:before {
  transform: translate(0, 0);
}

.border-top,
.border-right,
.border-bottom,
.border-left {
  position: absolute;
  z-index: 0;
  pointer-events: none;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  overflow: hidden;
}

.border-top:before,
.border-right:before,
.border-bottom:before,
.border-left:before {
  content: " ";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #21DBB2;
  transition: transform 0.3s ease;
}

.border-top {
  bottom: auto;
  height: 2px;
}

.border-top:before {
  transform: translate(-101%, 0);
  transition-delay: 0.3s;
  background-image: linear-gradient(to right, #00DDB7, #00E1C8);
}

.draw .border-top:before {
  transition-delay: 0s;
}

.border-right {
  left: auto;
  width: 2px;
}

.border-right:before {
  transform: translate(0, -100%);
  transition-delay: 0s;
  background-image: linear-gradient(to bottom, #00E1C8, #00DBB0);
}

.draw .border-right:before {
  transition-delay: 0.3s;
}

.border-bottom {
  top: auto;
  height: 2px;
}

.border-bottom:before {
  transform: translate(-101%, 0);
  transition-delay: 0s;
  background-image: linear-gradient(to right, #00D9A0, #00DBB0);
}

.draw .border-bottom:before {
  transition-delay: 0.3s;
}

.border-left {
  right: auto;
  width: 2px;
}

.border-left:before {
  transform: translate(0, -100%);
  transition-delay: 0.3s;
  background-image: linear-gradient(to bottom, #00DDB7, #00D9A0);
}

.draw .border-left:before {
  transition-delay: 0s;
}

.green-color {
