.btn {
  display: inline-block;
  position: relative;
  height: 50px;
  line-height: 50px;
  background: none;
  font-family: "FuturaPT-Demi", Helvetica, Arial, sans-serif;
  font-weight: normal;
  font-size: 12px;
  letter-spacing: 0.1em;
  color: #FFF;
  text-transform: uppercase;
  text-decoration: none;
  text-align: center;
  overflow: hidden;
}

.btn:hover .btn__fill, .btn:focus .btn__fill {
  transform: translate3d(0, 0, 0);
}

.btn:hover .btn__text, .btn:focus .btn__text {
  color: #333;
}

.btn[disabled], .btn:disabled {
  opacity: 0.5;
  cursor: default;
}

.btn.-width-fluid {
  display: block;
  width: 100%;
}

.btn.-width-250 {
  width: 250px;
}

.btn.-type-filled:after {
  content: " ";
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #21DBB2;
  background-image: linear-gradient(-20deg, #20D8A2 0%, #21E0C7 100%);
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease;
}

.btn.-type-filled:hover:after {
  opacity: 1;
  visibility: visible;
}

.draw .btn.-type-filled .btn__fill {
  transform: none;
  transition-delay: 0.6s;
}

.draw .btn.-type-filled .btn__text {
  color: #333;
  transition-delay: 0.65s;
}

.btn.-height-sm {
  height: 40px;
  line-height: 40px;
}

.btn.-color-grey .btn__fill {
  background: #777;
}

.btn.-color-grey .borders > *:before {
  background: #777;
}

.btn__text {
  position: relative;
  z-index: 2;
  transition: color 0.2s ease;
  transition-delay: 0.05s;
}

.btn__fill {
  position: absolute;
  z-index: 0;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  transform: translate3d(-101%, 0, 0);
  transition: transform 0.3s ease;
  background: linear-gradient(20deg, #20D8A2 0%, #21E0C7 100%);
}

.btn-round {
  position: relative;
  height: 40px;
  box-sizing: border-box;
  background: none;
  color: #FFF;
  font-family: "FuturaPT-Demi", Helvetica, Arial, sans-serif;
  font-weight: normal;
  font-size: 14px;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  text-decoration: none;
  box-shadow: inset 0 0 0 1px #777;
  border-radius: 20px;
  overflow: hidden;
  text-align: center;
  transition: box-shadow 0.2s ease;
  transform: translate3d(0, 0, 0);
}

.btn-round:before {
  content: "";
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 20px;
  background-color: #21DBB2;
  background-image: linear-gradient(20deg, #20D8A2 0%, #21E0C7 100%);
  transform: translate(-100%, 0);
  transition: transform 0.3s ease;
}

.btn-round:hover {
  box-shadow: inset 0 0 0 1px #21DBB2;
}

.btn-round:hover:before {
  transform: translate(0, 0);
}

.btn-round.is-active {
  box-shadow: inset 0 0 0 1px #21DBB2;
}

.btn-round.-width-180 {
