  position: relative;
  padding-left: 16px;
  margin-top: 15px;
}

.lesson__list-level-2 > li:before {
  content: "";
  position: absolute;
  top: 8px;
  left: 0;
  width: 12px;
  height: 0;
  border-top: 1px solid;
}

.lesson__grey-box {
  position: relative;
  padding-left: 60px;
}

.lesson__grey-box:before {
  content: "";
  position: absolute;
  z-index: -1;
  top: -10px;
  bottom: -10px;
  left: 60px;
  right: calc(-0.5 * (100vw - 1140px));
  background: #545454;
}

.lesson__grey-box .lesson__list-level-2 {
  margin-left: 0;
}

.lesson__grey-box .lesson__list-level-2:before {
  display: none;
}

.lesson__list-label {
  display: block;
  margin: 15px 0;
}

.lessons-counter {
  position: relative;
  box-sizing: border-box;
  font-family: "FuturaPT-Heavy", Helvetica, Arial, sans-serif;
  font-weight: normal;
  font-size: 14px;
  letter-spacing: 0.1em;
  color: #777;
  line-height: 1.5;
  padding-right: 120px;
}

.lessons-counter:after {
  content: "";
  position: absolute;
  top: 9px;
  right: 0;
  width: 100px;
  border-top: 2px solid;
}

.lessons-counter .current {
  color: #FFF;
}

.lessons-counter__label {
  display: inline-block;
  vertical-align: top;
  letter-spacing: 0.5em;
  text-transform: uppercase;
}

.lessons-counter__numbers {
  display: inline-block;
  vertical-align: top;
}

.lessons-counter__current {
  color: #FFF;
}

.slider-duo {
  position: relative;
}

.slider-duo:after {
  content: " ";
  display: table;
  clear: both;
}

.slider-duo__nav-buttons {
  position: relative;
  z-index: 2;
  max-width: 810px;
  margin-left: -20px;
}

@media only screen and (min-width: 1300px) {
  .slider-duo__nav-buttons {
    max-width: 880px;
    margin-left: -90px;
  }
}

.slider-duo__nav-prev,
.slider-duo__nav-next {
  position: absolute;
  top: 0;
  background: none;
  color: rgba(255, 255, 255, 0.3);
  transition: all 0.2s ease;
  font-size: 24px;
  height: 130px;
}

.slider-duo__nav-prev:hover,
.slider-duo__nav-next:hover {
  color: #21DBB2;
}

.slider-duo__nav-prev.slick-disabled,
.slider-duo__nav-next.slick-disabled {
  opacity: 0;
  visibility: hidden;
}

.slider-duo__nav-prev {
  left: 0;
}

.slider-duo__nav-next {
  right: 0;
}

.slider-duo__left {
  position: relative;
  z-index: 1;
  float: left;
  width: 66.66667%;
  min-height: 440px;
}

.slider-duo__left .slick-list {
  overflow: visible;
}

.slider-duo__left-inner {
  margin-left: -1000px;
  overflow: hidden;
}

.slider-duo__left-inner:after {
  content: " ";
  display: table;
  clear: both;
}

.slider-duo__left-slides {
  position: relative;
  float: right;
  max-width: 760px;
}

.slider-duo__left-slides:after {
  content: " ";
  display: table;
  clear: both;
}

.slider-duo__left-slides:before {
  content: "";
  position: absolute;
  z-index: -1;
  top: 0;
  right: 0;
  bottom: 0;
  background: #424242;
  left: 90px;
}

.slider-duo__right {
  position: relative;
  z-index: 0;
  float: right;
  width: 33.33333%;
}

.slider-duo__right .slick-list {
  overflow: visible;
}

.slider-duo__right-inner {
  margin-right: -1000px;
  overflow: hidden;
}

.slider-duo__right-slides {
  max-width: 340px;
  position: relative;
  left: -340px;
}

.slider-duo__right-slides:after {
  content: " ";
  display: table;
  clear: both;
}

.slider-duo__slide-count {
  display: block;
  height: 190px;
  font-family: "FuturaPT-Heavy", Helvetica, Arial, sans-serif;
  font-weight: normal;
  font-size: 12px;
  letter-spacing: 0.1em;
  color: #777;
}

.slider-duo__slide-count .current {
  color: #FFF;
  font-style: normal;
}

.slider-duo__slide-count:after {
  content: "";
  display: block;
  height: 110px;
  margin: 30px 0;
  border-left: 2px solid rgba(255, 255, 255, 0.3);
}

.slider-duo__slide-lg {
  position: relative;
  float: left;
  width: 100%;
  box-sizing: border-box;
  padding: 30px 110px 60px 0;
}

.slider-duo__slide-lg:before {
  opacity: 0;
  visibility: hidden;
  content: "";
  position: absolute;
  z-index: -1;
  top: 0;
  right: 30px;
  bottom: 0;
  background: #424242;
  left: 90px;
  transition: all 0.2s ease;
}

.slider-duo__slide-lg:hover:before {
  opacity: 1;
  visibility: visible;
}

.slider-duo__slide-lg.slick-slide {
  opacity: 0.5;
  transition: 0.3s linear;
  transition-property: filter, -webkit-filter, opacity;
}

.slider-duo__slide-lg.slick-current {
  opacity: 1;
  transition-duration: 0s;
  transition-delay: 0s;
}

.slider-duo__slide-lg.slick-current:before {
  opacity: 0;
  visibility: hidden;
}

.m-cssfilters .slider-duo__slide-lg.slick-slide {
  opacity: 1;
  filter: saturate(0);
}

.m-cssfilters .slider-duo__slide-lg.slick-current {
  filter: none;
}

.slider-duo__slide-sm {
  float: left;
  padding: 30px 0 0 60px;
  box-sizing: border-box;
  cursor: pointer;
}

.slider-duo__slide-sm.slick-slide .person__pic {
  opacity: 0.5;
}

.m-cssfilters .slider-duo__slide-sm.slick-slide .person__pic {
  opacity: 1;
  filter: saturate(0);
}

.person {
  color: #AAA;
  position: relative;
  z-index: 0;
}

.person:after {
  content: " ";
  display: table;
  clear: both;
}

.person.-size-sm .person__title,
.person.-size-sm .person__name {
  font-size: 15px;
}

.person.-size-sm .person__pic {
  width: 95px;
  height: 95px;
  margin-bottom: 25px;
}

.person.-size-sm .course-info__label {
  font-size: 12px;
}

.person.-size-sm .course-info__value {
  font-size: 14px;
}

.modal .person {
  max-width: 760px;
  margin: 100px auto;
  text-align: left;
  position: relative;
}

.modal .person .person__buttons .btn {
  display: none;
}

.modal .person .person__buttons .btn.js-modal-close {
  display: block;
}

.modal .person .person__more {
  display: block;
}

.modal .person:before {
  content: "";
  position: absolute;
  z-index: -1;
  top: -30px;
  left: 90px;
  right: -110px;
  bottom: -60px;
  background: #424242;
}

.person__left {
  float: left;
  width: 190px;
}

.person__right {
  float: right;
  width: calc(100% - 240px);
}

.person__pic {
  width: 190px;
  height: 190px;
  overflow: hidden;
  margin-bottom: 40px;
}

.person__pic img {
  display: block;
  max-width: 100%;
  max-height: 100%;
}

.person__name,
.person__title {
  display: block;
  font-family: "FuturaPT-Heavy", Helvetica, Arial, sans-serif;
  font-weight: normal;
  font-size: 18px;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  margin: 20px 0;
}

.person__name {
  color: #AAA;
}

.person__title {
  color: #777;
}

.person__title:not(:first-child) {
  margin-top: 60px;
}

.person__text {
  font-size: 16px;
  color: #AAA;
  line-height: 1.375;
}

.person__more {
  margin-top: 60px;
  display: none;
}

.person__links {
  color: #FFF;
  line-height: 1.3;
}

.person__links > li {
  position: relative;
  padding-left: 90px;
}

.person__links > li:before {
  content: "";
  position: absolute;
  top: 11px;
  left: 0;
  width: 60px;
  height: 2px;
  background: #FFF;
  opacity: 0.3;
}

.person__links a {
  display: inline-block;
  text-decoration: none;
  font-family: "FuturaPT-Heavy", Helvetica, Arial, sans-serif;
  font-weight: normal;
  font-size: 16px;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  color: #FFF;
  transition: color 0.2s ease;
}

.person__links a:hover {
  color: #21DBB2;
}

.person__buttons {
  margin-top: 40px;
}

.person__buttons .btn {
  display: block;
}

.person__buttons .btn:not(:first-child) {
  margin-top: 20px;
}

.person__buttons .btn.js-modal-close {
  display: none;
}

.slick-slide:not(.slick-current) .person__buttons .btn {
  pointer-events: none;
  cursor: default;
}

.timeline {
  margin: 30px 0;
}

.timeline__date {
  display: block;
  margin-bottom: 25px;
  font-family: "FuturaPT-Heavy", Helvetica, Arial, sans-serif;
  font-weight: normal;
  font-size: 12px;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  color: #FFF;
}

.timeline__body {
  padding-left: 38px;
  border-left: 2px solid rgba(255, 255, 255, 0.3);
}

.timeline__title {
  display: block;
  margin-bottom: 20px;
  font-family: "FuturaPT-Heavy", Helvetica, Arial, sans-serif;
  font-weight: normal;
  font-size: 16px;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  color: #FFF;
}
